.about {
  background: #fafafa;
  text-align: center;
  padding: 50px 0 20px;
}
.content-about-wrapper {
  padding: 20px 0;
  text-align: center;
}
.about-content-paragraph {
  text-align: left;
}
.about-content-paragraph-last {
  margin: 0 0 50px;
  text-align: left;
}
@media (width>750px) {
  .about {
    padding: 100px 0 50px 0;
  }
  .content-about-wrapper {
    padding: 50px 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 100px;
    text-align: left;
  }
}
.skills-box {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}
.my-skill {
  padding: 10px 20px;
  border-radius: 5px;
  color: #234e70;
  background: rgba(35, 78, 112, 0.3);
  font-weight: 700;
}
